.container {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: 80px;
  display: flex;
}

.content {
  width: 300px;
  background-color: rgba(249, 247, 220, .82);
  border: 3px rgba(118, 140, 110, .82);
  flex-wrap: wrap;
  display: flex;
  box-shadow: 0 0 10px 1px rgba(118, 140, 110, .82);
}

.item {
  box-sizing: border-box;
  color: rgba(118, 140, 110, .82);
  width: 100px;
  height: 100px;
  cursor: pointer;
  border: 2px solid rgba(118, 140, 110, .82);
  justify-content: center;
  align-items: center;
  font-size: 48px;
  transition: background .1s;
  display: flex;
}

.item:hover {
  background-color: rgba(241, 241, 241, .047);
}

.mbox {
  width: 450px;
  height: 360px;
  text-align: center;
  background-color: rgba(238, 236, 206, .82);
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  top: 47%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 0 20px 5px rgba(118, 140, 110, .82);
}

.headText {
  color: rgba(118, 140, 110, .82);
  font-size: 48px;
  line-height: 5cqmin;
}

.win {
  color: rgba(118, 140, 110, .82);
  font-size: 48px;
  font-weight: bold;
  transition: background-color .5s;
}

